import { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom/cjs/react-router-dom.min';
import styled from 'styled-components';
import './Detail.scss';




let 박스 = styled.div`
  padding: 20px;
  
`;

let 제목 = styled.h4`
  font-size: 25px;
  color: ${props => props.색상}
`;

let 마진 = styled.div`
  margin: 5px
`;

function Detail(props){

    let {id}  = useParams();
    let history = useHistory();
    let [알람,알람수정] = useState(true);
    let [입력값,입력값수정] = useState('');

    useEffect(() => {
      let 타이머 = setTimeout(() => {
        알람수정(false);
      },2000)
      return () => {clearTimeout(타이머)}
    },[]);

    return(
      
        <div className="container">
          <박스>
            <제목 className='red' 색상 = {'blue'}>
              상세페이지
            </제목>
          </박스>

          <input onChange={(e) => {
             입력값수정(e.target.value);
          }} />
          
          {입력값}

          {
            알람 === true
            ?(<div className='my-alert'>
                <p>매진 임박</p>
              </div>)
            : null
          }
          
          <div className="row">
            <div className="col-md-6">
              <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
            </div>
            <div className="col-md-6 mt-4">
              <h4 className="pt-5">{props.shoes[props.shoes[id].id].title}</h4>
              <p>{props.shoes[props.shoes[id].id].content}</p>
              <p>{props.shoes[props.shoes[id].id].price}</p>

              <Info 재고 = {props.재고}  />


              <button className="btn btn-danger" onClick={() => {props.재고변경(
                
              )}}>주문하기</button>
              <마진></마진>
              <button className="btn btn-danger" onClick={() => {
                  history.goBack();
              }}>뒤로가기</button>  
            </div>
          </div>
        </div> 
      
    )
  }

  function Info(props){
    return(
      <p>재고:{props.재고[0]}</p>
    )
  }

  export default Detail;